.fail{
  border: 2px dotted orange;
   
  -webkit-transition: border 0.5s linear;
   
  -o-transition: border 0.5s linear;
   
  transition: border 0.5s linear;
}

.pass{
  border: 2px solid green;
   
  -webkit-transition: border 0.5s linear;
   
  -o-transition: border 0.5s linear;
   
  transition: border 0.5s linear;
}

.rowstyle{
    margin: 10px;
    width: 300px;
    height: 100px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column
}


